import React from "react";
import { Grid2 as Grid, Box, Typography } from "@mui/material";
import { useTranslation } from "react-i18next";

function Board({ calledNumbers, maxNumber = 90 }) {
  const { t } = useTranslation();
  // Generamos los nmeros del 1 al maxNumber
  const numbers = Array.from({ length: maxNumber }, (_, i) => i + 1);

  // Estilo de la cuadrcula para hacerla responsiva
  const gridStyle = {
    display: "grid",
    gridTemplateColumns: "repeat(9, 1fr)", // 9 columnas para representar los nmeros
    gap: "4px", // Reducimos el espacio entre los nmeros
    width: "100%",
    height: "100%",
    justifyItems: "center",
    alignItems: "center",
    gridAutoRows: "minmax(40px, auto)", // Altura mnima de las filas
    overflowX: "hidden", // Evita el desbordamiento horizontal
    // Estilos responsivos
    "@media (max-width: 600px)": {
      gridTemplateColumns: "repeat(6, 1fr)", // En pantallas pequeas, reducimos a 6 columnas
      gap: "2px", // Reducimos an ms el espacio entre celdas
    },
    "@media (max-width: 400px)": {
      gridTemplateColumns: "repeat(5, 1fr)", // En pantallas muy pequeas, reducimos a 5 columnas
      gap: "1px", // Espacio an ms pequeo
    },
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        width: "100%",
        height: "100%",
      }}
    >
      <Typography variant="h6" sx={{ marginBottom: "16px" }}>
        {t("called numbers")}:
      </Typography>
      <Box sx={{ overflowY: "auto", flex: 1, width: "100%" }}>
        <Grid container spacing={2} sx={gridStyle}>
          {numbers.map((num) => (
            <Grid item key={num}>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  width: "30px", // Ajustamos el tamao de las celdas para pantallas pequeas
                  height: "30px", // Ajustamos el tamao de las celdas
                  backgroundColor: calledNumbers.has(num)
                    ? "green"
                    : "lightgray",
                  color: calledNumbers.has(num) ? "white" : "black",
                  borderRadius: "50%",
                  fontWeight: "bold",
                  fontSize: "1em", // Tamao de texto ajustado para pantallas pequeas
                  transition: "background-color 0.3s",
                }}
              >
                {num}
              </Box>
            </Grid>
          ))}
        </Grid>
      </Box>
    </Box>
  );
}

export default Board;
